.header{
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgray;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
    z-index: 999;
}
.header_left{
    display: flex;
}
.header_left > img{
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
    /* margin-left: 10px; */
}
.header_search{
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    color: gray;
    height: 22px;
    background: #eef3f8;
}
.header_search > input{
   outline: none;
   border: none;
   background: none;
}
.header_right{
    display: flex;

}